.service{
    height: 476px;
    width: 100%;
    overflow: hidden;
    -webkit-backface-visibility: hidden; /* add to fix webkit bug jitter */
    -webkit-transform: perspective(1000px); /* add to fix webkit bug jitter */
    margin-top: -1px;
    &__content{
        height: 100%;
        width: 100%;
    }

    .service-language-wrap{
        width: 110px;
        height: 28px;
    }

    .header{
        padding-bottom: 48px;
        height: 100px;
        p{
            @include dmsans32_500();
        }
        span{
            @include dmsans32();
            @include gradient();
        }
    }

    .service_menu{
        margin-top: -1px;
        overflow: hidden;
        width: 100%;
        display: flex;
        justify-content: center;
        cursor: pointer;
        box-shadow: 0px 0px 0px 420px var(--white-color);
        .service_box{
            width: 665px;
            height: 376px;
            position: relative;
            color:white;
            transition: background-color .5s ease-out, color .5s ease-out; 
            path{
                fill: url(#paint0_linear_228_306);
            }
            circle{
                fill: white;
            }
            .content-wrap{
                position: absolute;
                top: 50%;
                .service_text{
                    transform-origin: 0;
                }
                h1{
                    @include dmsans48_500();
                    margin-right: 1rem;
                }
                h2{
                    @include dmsans48();    
                }

                

                .show_more{
                    padding-top: 42px;
                    display: flex;
                    align-items: center;
                    svg{
                        width: 32px;
                        height: 32px;
                    }
                    p{
                        @include dmsans16();
                        display: inline;
                        padding-left: 16px;
                    }
                
                }
            }
        }
        .service_header{
            display: inline;
        }
        .right{
            box-shadow: 500px 0 var(--white-color);
            margin-right: -1px;
            .content-wrap{
                padding-left: 64px;
            }
        }
        .left{
            box-shadow: -500px 0 var(--white-color);
            .content-wrap{
                right: 205px
            }
        }
    }
}

.notactive{
    background: #ffffffe0;
    .content-wrap{     
        color: var(--black-color);
        .show_more{
            svg{
                path{
                    fill: white;
                }
                circle{
                    fill: url(#paint0_linear_228_306);
                }
            }
        }
    }
}