.footerinfo{
    position:relative;
    //min-height: 680px;
    background-image: url("./images/bg/footer_dark.png");
    background-position: center;
    background-size: cover;
    color: var(--cultured_white-color);
    .footerinfo-wrap{
        padding-top: 215px;
    }
    .logo{
        height: 78px;
        border-bottom: 1px solid var(--border-color);
        img{
            padding-left: 64px;
            padding-top: 23px;
            height: 35px;
        }
    }
    .container{
        height: 441px;
        display: flex;
        border-left: 1px solid var(--border-color);
        border-right: 1px solid var(--border-color);
        a{
            position: relative;
            display: table;
            &:link, &:visited,&:active{
                text-decoration: none;
                color: var(--cultured_white-color);
            }
              
            &:hover{
                &::before{
                    width: 100%;
                }
            }

            &::before{
                content: "";
                position: absolute;
                width: 0;
                height: 2px;
                background-color: #FFFFFF;
                bottom: -2px;
                left: 0;
                transition: 0.3s width;
            }
        }

        .notactive{
            background:none;
            &::before{
                width: 100%;
            }
        }

        h1{
            @include dmsans24();
            padding-bottom: 32px;
            padding-top: 152px;
        }
        p{
            @include dmsans20();
        }
        .left{
            padding-left: 54px;
        }
        .right{
            padding-left: 130px;
        }
        .fifty{
            flex: 0 0 50%;
            display: flex;
        }
        .first {
            border-right: 1px solid var(--border-color);
            .text{
                padding-left: 111px;
            }
        }
    }

    .copyright{
        height: 79px;
        border-top: 1px solid var(--border-color);
        display: flex;
        justify-content: space-between;
        align-items: center;
        p{
            padding: 0px 63px 0px 63px;
            @include dmsans14();
        }
    }
}