.where{
    height: calc(100vh - 80px + 100px);
    padding-top: 350px;
    position: relative;
    overflow: hidden;
    margin-top: -1px;
    //background-color: white;
    display: flex;
    justify-content: center;
    padding-bottom: 100px;
    &__contentWrap{ 
        height: calc(100% - 350px);
        position: absolute;
        top: 350px;
    }

    &__circleWrap{
        position: absolute;
        width: 100%;
        height: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        &__circle{
            position: absolute;
            width: 1330px;
            height: 1330px;
            z-index: -1;
            background-color: transparent;
            border-radius: 100%;
            box-shadow: 0px 0px 0px 3000px var(--white-color);
        }
        &__innercircle{
            position: absolute;
            z-index: -1;
            width: 1013px;
            height: 1013px;
            background-color: var(--white-color);
            border-radius: 100%;
        }
    }
    
    .where__reveal{
        display: flex;
        height: 100%;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        a{
            text-decoration: none;
            &:link, &:visited,&:active{
                text-decoration: none;
            }
        }
        &__first{
            transform: translateY(80px);
            max-width: 907px;
            text-align: center;
            padding-bottom: 91px;
            opacity: 0;
            transition: opacity 0.3s ease-out;
            width: 907px;
            height: 300px;
            position: relative;
            p{
                @include dmsans92();
            }
            span{
                @include dmsans92_500();
                @include gradient2();
            }
        }
        &__second{
            display: flex;
            //transform: translateY(80px);
            opacity: 0;
            transition: opacity 0.3s ease-out;
            background-image: url("./images/bg/btn-bg.png");
            .box{
                transition: .7s ease-out;
                display: flex;
                justify-content: center;
                align-items: center;
                width: 320px;
                height: 96px;
                background: #ffffffe0;
                color: var(--black-color);
                p{
                    mix-blend-mode: none;
                    @include dmsans24();
                }
            }
            .active{
                background: none;
                color:var(--white-color);
            }
        }
    }
}