.reasons{
    background-color: var(--white-color);
    min-height: 1319px;
    //padding-bottom: 200px;
    .swap-wrapper-reasons{
        width: 100%;
        position: relative;
    }
    .headline-content{
        height: 344px;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        position: relative;
    }
    .container{
        padding-top: 200px;
        .headline{
            height: 544px;
            h1{
                @include dmsans92();
                padding-bottom: 48px;
            }
            p{
                @include dmsans28();
                padding-bottom: 200px;
            }
            span{
                @include gradient();
            }
        }
        .bulletpoints{
            width: 100%;
            padding-bottom: 1px;
            p{
                transition: 0.3s ease-out;
                @include dmsans32();
                max-width: 1330px;
            }
            .row{
                min-height: 112px;
                width: 100%;
                border-top: 1px solid #DFDFDF;
                display: flex;
                align-items: center;
                position: relative;
                .dot{
                    transition: 0.3s ease-in-out;
                    width: 21px;
                    height: 21px;
                    border-radius: 100%;
                    background: linear-gradient(90deg, var(--brown-color), var(--bronze-color));
                    opacity: 0;
                    position: absolute;
                }
            }

            .row:hover{
                p{
                    transform: translateX(54px);
                }
                .dot{
                    opacity: 100;
                }
            }

            .last{
                border-bottom: 1px solid #DFDFDF;
            }
            .lastlast{
                border-top: 0px;
                border-bottom: 1px solid #DFDFDF;
            }
            .lastlast.untoggled_service{
                display: none;
            } 
        }
    }
}