.video{
    margin-top: -1px;
    background-color: var(--white-color);
    padding-bottom: 200px;
    .container{
        aspect-ratio: 16 / 9;
        background-image: url('./images/bg/video_placeholder.png');
        display: flex;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        position: relative;
        iframe{
            position: absolute;
            opacity: 0;
            transition: opacity 1.5s ease-in-out;
        }
        .video_square{
            position: absolute;
            display: flex;
            justify-content: center;
            align-items: center;
            .video_circle{
                width: 0px;
                height: 0px;
                border-radius: 100%;
                background-color: transparent;
                box-shadow: 0 0 0 900px white;
            }
        }
    }
}