.hero{
    position: relative;
    width: 100%;
    height: 724px;
    padding-top: 80px;
    overflow: hidden;
    .hero-language-wrap{
        width: 180px;
    }
    .container{
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        box-shadow: 0px 0px 0px 1000px var(--white-color);
        .hero__wrap{
            overflow: hidden;
            box-shadow: 0px 0px 0px 300px var(--white-color);
            &__content{
                display: flex;
            }
        }
        .big_text{
            background-color: var(--white-color);
            box-shadow: 0px 0px 0px 400px var(--white-color);
            p{
                @include dmsans92();
                overflow: hidden;
                height: 100px;
            }
            .hero__wrap__content{
                width: 650px;
                height: 100px;
            }
            span{
                @include gradient();
            }
        }
        .fifty{
            overflow: hidden;
            flex: 50%;
            height: 100%;
            display: flex;
            align-items: center;
            
        }
        .right{
            justify-content: center;
        }

        .numbers{
            background-color: transparent;
            box-shadow: 0px 0px 0px 300px var(--white-color);
            overflow: hidden;
            img{
                margin-right: 40px;
                
            }
            .hero__first{
                display: flex;
                padding-bottom: 138px;
                padding-top: 1px;
                overflow: hidden;
                img{
                    box-shadow: 0px 0px 0px 300px var(--white-color);
                }
            }
            .hero__second{
                display: flex;
                //box-shadow: 0px 0px 0px 300px #FFFFFF;
                padding-left: 5px;
                padding-top: 1px;
                overflow: hidden;
                img{
                    box-shadow: 0px 0px 0px 300px var(--white-color);
                }
            }
            @include dmsans24_500();
            
        }
    }
}